export default {
    props: {
        data_store: {
            type: [String, Array, Object],
            default: "",
        },
        setData: {
            type: Function
        }
    },
    data() {
        return {
            fa_name: null,
            en_name: null,
            description: null,
            province: null,
            city: null,
            phone_number: null,
            logo: null,
            name_option: null,
            address_option: null,
            email_option: null,
            phone_option: null,
            shop_terms : null
        }
    },
    watch : {
      logo : function (){

          this.setLogo()
      }
    },
    methods : {
        setLogo(){
            alert(this.logo)
            this.setData('logo',this.logo)
        }
    }

};
